
import './index.css';
import * as serviceWorker from './serviceWorker';
import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { BrowserRouter as Router, Route } from 'react-router-dom';

import './css/linearicons.css';
import './css/font-awesome.min.css';
import './css/themify-icons.css';
import './css/bootstrap.css';
import './css/main.css';

import 'jquery';
import 'bootstrap';

import DashBoard  from '../src/screen/MainScreen';
import MainReactjsScreen  from '../src/screen/MainReactjsScreen';
import DetailScreen from './screen/DetailScreen';

ReactDOM.render(
    <Provider>
    <div>
      <Router>
        <div>
          <Route exact path={'/'} component={DashBoard} />
          <Route exact path={'/React-js'} component={MainReactjsScreen} />
          <Route exact path={'/ReactNative-code'} component={DetailScreen} />
        </div>
      </Router>
    </div>
  </Provider>
  , document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
