import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Prism from 'prismjs';

class DetailScreen extends Component {

    componentDidMount(){
        setTimeout(() => Prism.highlightAll(), 0)
    }

    render() {
        const state = this.props.location.state.imagePath;
        const title = this.props.location.state.title;
        const code = this.props.location.state.code;
        return (
            <div className="">
                <nav class="navbar navbar-b navbar-trans navbar-expand-md" style={{backgroundColor: '#000000'}} id="mainNav">
                    <div class="container">
                    <Link class="navbar-brand" to="/">CodeForDevApps</Link>
                    <button class="navbar-toggler collapsed" type="button" data-toggle="collapse" data-target="#navbarDefault"
                        aria-controls="navbarDefault" aria-expanded="false" aria-label="Toggle navigation">
                        <span></span>
                        <span></span>
                        <span></span>
                    </button>
                    <div class="navbar-collapse collapse justify-content-end" id="navbarDefault">
                        <ul class="navbar-nav">
                        <li class="nav-item">
                            <Link class="nav-link" to="/">React Native</Link>
                        </li>
                        <li class="nav-item">
                            <Link class="nav-link" to="./React-js">React js</Link>
                        </li>
                        <li class="nav-item">
                            <Link class="nav-link" to="#contact">Contact</Link>
                        </li>
                        </ul>
                    </div>
                    </div>
                </nav> 
                <div style={{textAlign: 'center', color: 'black', marginTop: '30px'}}><h2>{title}</h2></div>
                <div style={{marginTop: '30px'}}>
                    <div class="container">
                        <div class="row">
                            <div class="col-sm-4">
                                {state &&
                                <img className="img-fluid" src={state} alt=""/>
                                }
                            </div>
                            <div class="col-sm-8"  style={{borderLeft:'solid 4px #000000', marginTop: '25px', overflow:'scroll', overflowX:'hidden', overflowY: 'hidden'}}>
                                <div style={{marginTop: '10px'}}>
                                    <pre className="line-numbers">
                                        <code className="language-js">{code}</code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div style={{marginTop: '30px'}}>
                    <div style={{backgroundColor: 'black'}}>
                        <p style={{textAlign: 'center', color: 'white', padding: '20px'}}>
                            Created by Vijayakumar KTM
                        </p>
                    </div>
                </div>
            </div>
          );
    }
}

export default DetailScreen;